@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap');
*{
    margin: 0px;
    padding: 0px;
    
}

.cointainer {
  height: 100vh;
  overflow-y: scroll;
  background-color: #e6f2fd;
}

  /* Styles for the heading */
  .heading {
    text-align: center;
    padding: 2rem;
  }
  
  .heading h1 {
    font-size: 3rem;
    font-weight: bold;
    font-family: 'Roboto Slab', serif;
  }
  
  /* Styles for the projects */
  .projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  /* Styles for individual projects */
  .projectCommon {
    width: 34vw;
    margin: 15px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease-in-out;
  }
  
  .projectCommon:hover {
    transform: translateY(-10px);
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
  }
  
  .project-img{
    display: flex;
    overflow: auto;
    scroll-snap-type: mandatory;
    scroll-behavior: smooth;
  }

  .project-img img {
    width: 100%;
    height: auto;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }
  
  .project-content {
    padding: 20px;
  }
  
  .project-content h1 {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .project-content p {
    font-size: 1rem;
    line-height: 1.5;
    color: #555;
  }
  
  /* Media queries for responsiveness */
  
  @media (max-width: 991px) {
    .projectCommon {
      width: calc(50% - 30px);
    }
  }
  
  @media (max-width: 767px) {
    .projectCommon {
      width: 100%;
    }
  }
  
  @media (max-width: 480px) {
    .projectCommon:hover {
      transform: none;
    }
  }
  
