*{
    margin: 0px;
    padding: 0px;
}
    .form-container{
      font-family: 'Montserrat', sans-serif;
      background-color: #333;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 100vh;
    }
  
  h2 {
    font-size: 2.5rem;
    text-align: center;
    margin-bottom: 30px;
    text-shadow: 2px 2px 2px rgba(1, 1, 1, .5);
  }
  
  form {
    background: #fff;
    padding: 30px;
    border-radius: 20px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  input[type=text],
  textarea {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    color: #333;
    border: none;
    border-radius: 10px;
    background: #F2F2F2;
    margin-bottom: 20px;
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    transition: all 0.3s ease;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.05);
  }
  
  input[type=text]:focus,
  textarea:focus {
    background: #fff;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
    outline: none;
  }
  
  input[type=submit] {
    background: linear-gradient(to right, #4a90a6 0%, #58a4b0 100%);
    color: #fff;
    border: none;
    padding: 15px 30px;
    font-size: 18px;
    border-radius: 30px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
    font-weight: 600;
    letter-spacing: 1px;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  }
  
  input[type=submit]:hover {
    background: linear-gradient(to right, #58a4b0 0%, #4a90a6 100%);
  }
  
  label {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 10px;
    margin-top: 20px;
    letter-spacing: 1px;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  }
  
  /* Media Queries */
  @media screen and (max-width: 600px) {
    input[type=text],
    textarea {
      margin-bottom: 10px;
    }
  
    input[type=submit] {
      padding: 10px 20px;
      font-size: 16px;
    }
  
    h2 {
      font-size: 28px;
      margin-bottom: 20px
  
    }

    label {
    font-size: 16px;
    margin-top: 10px;
    }
    }
