
*{
    margin: 0px;
    padding: 0px;
}
  
  .container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    text-align: center;
  }
.container-contact {
    display: flex;
    position: absolute;
    top: 1rem;
    right: 2rem;
}

.container-contact img{
  height: 1.5rem;
  margin: 0.5rem;
  cursor: pointer;
}

  .container h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  
  .container p {
    margin-bottom: 1.5rem;
    font-size: 2rem;
  }
  
  .container-img img {
    border-radius: 50%;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.4);
    max-width: 100%;
  }
  
  

  
  
