/* Style for About Me section */
.description-container {
  background-color: #e6f2fd;
    font-family: 'Roboto Slab', serif;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  
  .heading-aboutme {
    text-align: center;
    text-decoration: underline;
  }
  
  .heading-aboutme h1 {
    font-size: 2.5rem;
    font-weight: bold;
    text-transform: uppercase;
  }
  
  .desciption-aboutme {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;
  }
  
  .skill {
    width: 50%;
  }
  
  .college {
    width: 50%;
  }
  
  .skill-heading,
  .college-name {
    font-size: 1.5rem;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  
  .skill-description,
  .description {
    font-size: 1.2rem;
    margin-top: 10px;
    font-weight: bold;
    text-align: center;
  }
  
  .skill-description ul,
  .description {
    list-style-type: none;
  }
  
  .skill-description li {
    background-color: #333;
    color: #fff;
    padding: 10px 20px;
    border-radius: 20px;
    margin: 10px;
    font-size: 1rem;
    text-align: center;
  }
  
  @media only screen and (max-width: 768px) {
    .desciption-aboutme {
      flex-direction: column;
      gap: 1rem;
    }
    
    .skill,
    .college {
      width: 100%;
    }
  
    .heading-aboutme h1 {
      font-size: 2rem;
    }
    
    .skill-heading,
    .college-name {
      font-size: 1.2rem;
    }
  
    .skill-description li {
      font-size: 0.8rem;
      padding: 5px 10px;
    }
  
    .description {
      font-size: 1rem;
    }
  }
  