
.navbar {
  display: flex;
  flex-direction: column;
  position: absolute;
  height: 100vh;
  width: 10rem;
  align-content: center;
  flex-wrap: wrap;
  list-style: none;
  background: #435454;
  font-size: 1.5rem;
  z-index: 2;
}

.hambarger{
  position: absolute;
  top: 1rem;
  left: 0.25rem;
  cursor: pointer;
}

.ham-class {
  background-color: black;
  height: 5px;
  width: 30px;
  margin: 5px;
  border-radius: 26%;
}

.cross{
  position: absolute;
  font-size: 4.2rem;
  right: 6px;
  top: 50%;
  cursor: pointer;
}

.navbar ul{
  list-style: none;
}

.navbar ul li{
  margin: 1.3rem;
  text-align: center;
}

.navbar ul li a{
  text-decoration: none;
  color: aliceblue;
}

.navbar ul li a:hover{
  background-color: antiquewhite;
  border-radius: 20%;
  color: black;
  transition: all .5s ease-in-out;
 }

.display{
  display: none;
  transition: all 2s ease-in;
}







